import React from 'react';
import './Building.css';
import { Text, Stage, Layer, Rect, Circle, Line } from 'react-konva';

class Building extends React.Component {
  constructor(props) {
    super(props)
  }
  render() {

    const area = this.props.building.area
    const floors = this.props.building.floors
    const totalArea = area*floors
    const perimeter = Math.floor(Math.sqrt(area)*4)
    const wallArea = perimeter*(floors*2.5)
    const roofArea = Math.floor(area*1.1)
    const windowArea = Math.floor(wallArea*0.15)
    const built = this.props.building.built
    const age = 2021-built

    if (this.props.building.selectAddress === true && this.props.building.selectEdit === '') {
    return (
      <div className="Building">
        <div className="image-container">
          {/*<BuildingView building={this.props.building}/>*/}
          <img src={this.props.building.imageSrc}></img>
        </div>
        <h2 className='address-title'>{this.props.building.address}</h2>
        <div className='information-container'>
          <div className="address-information">
            <p>Etageareal:</p>
            <p>Etager:</p>
            <p>Samlet areal</p>
            <p>Omkreds:</p>
            <p>Vægareal:</p>
            <p>Tagareal:</p>
            <p>Vinduesareal:</p>
          </div>
          <div className='address-values'>
            <p>{area}</p>
            <p>{floors}</p>
            <p>{totalArea}</p>
            <p>{perimeter}</p>
            <p>{wallArea}</p>
            <p>{roofArea}</p>
            <p>{windowArea}</p>
          </div>
          <div className="address-information" id='add2'>
            <p>Opført:</p>
            <p>Alder:</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
          </div>
          <div className='address-values'>
            <p>{built}</p>
            <p>{age}</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
            <p>'</p>
          </div>
          <Stage width={250} height={250} className='imageStage'>
            <Layer>
              <Rect x={0} y={50} width={100} height={50} fill="#537dcc" />
              <Rect x={0} y={150} width={50} height={100} fill="#537dcc" />
              <Line x={100} y={150} points={[0,0, 100,0, 50,-50, 0,0]} closed /*stroke="black"*/ fill="#537dcc" />       
            </Layer>
          </Stage>
        </div>
      </div>
    )
    } else {
      return(
        <div></div>
      )
    }
  }
}
export default Building;